
import iHeader from "@/components/header/header-r.vue";
import { TabContainer, TabContainerItem, LoadMore, Popup } from "mint-ui";
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import XHeader from 'vux/src/components/x-header/index.vue'

import "@/assets/css/main.css";
export default {
name: "credit-loan",
components: {
iHeader,
TabContainer,
TabContainerItem,
Popup,
LoadMore,
Tab,
TabItem,
XHeader
},
data() {
return {
users: localStorage.getItem("users")
? JSON.parse(localStorage.getItem("users"))
: "",
msg: "这个是信用贷页面",
title: this.$route.query.name,
arrow: true,
alert: false,
active: "sq_content",
defaultImg: 'this.src="' + require('@/assets/images/head.png') + '"',
alertimg: [
{
src: require("@/assets/images/my/alert-icon3.png"),
icon: "态度热情，专业能力很强，细致耐心。"
},
{
src: require("@/assets/images/my/alert-icon2.png"),
icon: "专业能力极强，客户一致好评，值得信赖。"
},
{
src: require("@/assets/images/my/alert-icon1.png"),
icon: "最靠谱最贴心的伙伴，好评如潮，值得亲睐。"
}
],
rm: [],
gr: [],
gs: [],
cityselected: 0,
cities: [],
timeselected: 0,
times: [],
moneyselected: 0,
moneys: [],
typeselected: 0,
types: [],
page: 1,
popupVisible: false,
allLoaded: false,
reloadState:'',
getBarWidth: function (index) {
return (index + 1) * 22 + 'px'
}
};
},
methods: {
post_handle(){
if(this.users.is_manager==1){
this.$router.push('/loan/push-product');
}else{
mui.toast("请先注册客户经理")
}

},
tabBtnClick(type) {
this.active = type;
if (type == "sq_content") {
this.getlenders();
return false;
} else if (type == "all_content") {
this.gethot();
return false;
} else if (type == "sh_content") {
$('#loan').css('background','#fff');
this.gethot();
return false;
}
},
hidemsg: function() {
this.alert = false;
},
hidemsgs: function(ev) {
ev.preventDefault();
this.alert = true;
},
alertmsg(ev) {
ev = ev || window.event;
ev.preventDefault();
this.alert = true;
},
changeSelect() {
this.$axios.post(
"/api/Product/product_list",
`data=${JSON.stringify({
type:"LENDERS",
page: this.page,
city: this.cityselected,
pro_time: this.timeselected,
pro_money: this.moneyselected,
pro_type: this.$route.query.id
})}`
)
.then(res => {
console.log(res);
if (res.code == 200) {
this.gr = res.data.list;
}
})
.catch(err => {
throw err;
});
this.$axios.post(
"/api/Product/product_list",
`data=${JSON.stringify({
type: "HOT",
page: this.page,
city: this.cityselected,
pro_time: this.timeselected,
pro_money: this.moneyselected,
pro_type:this.$route.query.id
})}`
)
.then(res => {
console.log(res);
if (res.code == 200) {
this.gr = res.data.list;
}
})
.catch(err => {
throw err;
});
},
getlenders() {

this.$axios.post("/api/Product/product_list",`data=${JSON.stringify({type: "LENDERS",pro_type:this.$route.query.id,page: this.page})}`).then(res => {
console.log(res);
if (res.code == 200) {
this.gr=res.data.list;

this.$nextTick(function() {
const _this = this;
let getlength = res.data.list.length;
if (getlength <= res.data.pageSize) {
$(".mint-loadmore-bottom").text("暂无更多数据");

this.allLoaded = true;
}
if (this.page > 1) {
setTimeout(() => {
_this.popupVisible = true;
_this.reloadState = "加载成功";
_this.$refs.grLoadmore.onBottomLoaded();
}, 1000);
setTimeout(() => {
_this.popupVisible = false;
}, 2000);
} else {
setTimeout(() => {
_this.popupVisible = true;
_this.reloadState = "刷新成功";
_this.$refs.grLoadmore.onTopLoaded();
}, 1000);
setTimeout(() => {
_this.popupVisible = false;
}, 2000);
}
});
}
})
.catch(err => {
throw err;
});
},
gethot() {

/* this.$axios.post('/api/Product/product_list',`data=${JSON.stringify({
type:'HOT',
page:this.page,
})}`).then((res)=>{
console.log(res)
if(res.code==200){
this.rm=res.data.list
}
}).catch((err)=>{throw err}); */

this.$axios.post(
"/api/Product/product_list",
`data=${JSON.stringify({
type: "HOT",
page: this.page
})}`
)
.then(res => {

if (res.code == 200) {
this.rm = { ...this.rm, ...res.data.list };
this.$nextTick(function() {
let getlength = res.data.list.length;
if (getlength <= res.data.pageSize) {
$(".mint-loadmore-bottom").text("暂无更多数据");
this.allLoaded = true;
}
if (this.page > 1) {
setTimeout(() => {
this.popupVisible = true;
this.reloadState = "加载成功";
this.$refs.rmLoadmore.onBottomLoaded();
}, 1000);
setTimeout(() => {
this.popupVisible = false;
}, 2000);
} else {
setTimeout(() => {
this.popupVisible = true;
this.reloadState = "刷新成功";
this.$refs.rmLoadmore.onTopLoaded();
}, 1000);
setTimeout(() => {
this.popupVisible = false;
}, 2000);
}
});
}
})
.catch(err => {
throw err;
});
},
grLoadTop() {
this.page = 1;
this.getlenders();
},
grLoadBottom() {
this.page++;
this.getlenders();
},
gsLoadTop() {
this.page = 1;

},
gsLoadBottom() {
this.page++;

},
rmLoadTop() {
this.page = 1;
this.gethot();
},
rmLoadBottom() {
this.page++;
this.gethot();
}
},
computed:{




},
created() {
this.getlenders();


/*    this.$axios.post('/api/Product/product_list',`data=${JSON.stringify({
type:'COMPANY',
page:1,
})}`).then((res)=>{
console.log(res)
if(res.code==200){
this.gs=res.data.list
}
}).catch((err)=>{throw err}); */
},
mounted() {

var screen_h = document.documentElement.clientHeight;

window.onresize = function () {
var cur_h = document.documentElement.clientHeight;

if (screen_h === cur_h) {
$(".main").css('bottom','56px');
$('.tabbar').css('display','block');
}else{
$(".main").css('bottom','0');
$('.tabbar').css('display','none');
}
};
},
beforeCreate() {
this.$axios
.post("/api/Product/get_product_map")
.then(res => {
if (res.code == 200) {
console.log(res);
this.cities = res.data.city;
this.cities.unshift({ city: 0, city_name: "请选择城市" });
this.moneys = res.data.money;
this.moneys.unshift({ pro_money: 0, pro_money_name: "金额" });
this.times = res.data.time;
this.times.unshift({ pro_time: 0, pro_time_name: "期限" });
this.types = res.data.type;
this.types.unshift({ pro_type: 0, pro_type_name: "筛选" });
} else if (res.code == 999) {
Toast(res.msg);
}
})
.catch(err => {
throw err;
});
}
};



